import EmblaCarousel from "embla-carousel";
import "./carousel.css";
import { addPrevNextBtnsClickHandlers } from "./buttons";
import { addDotBtnsAndClickHandlers } from "./dots";
const OPTIONS = { loop: false };
export function initCarousel(emblaNode, options = {}) {
    options = { ...OPTIONS, ...options };
    const viewportNode = emblaNode.querySelector(".embla__viewport");
    const prevBtnNode = emblaNode.querySelector(".embla__button--prev");
    const nextBtnNode = emblaNode.querySelector(".embla__button--next");
    const dotsNode = emblaNode.querySelector(".embla__dots");
    if (!viewportNode) {
        console.log("no viewport node");
        return;
    }
    const emblaApi = EmblaCarousel(viewportNode, options);
    if (prevBtnNode && nextBtnNode) {
        const removePrevNextBtnsClickHandlers = addPrevNextBtnsClickHandlers(emblaApi, prevBtnNode, nextBtnNode);
        emblaApi.on("destroy", removePrevNextBtnsClickHandlers);
    }
    if (dotsNode) {
        const removeDotBtnsAndClickHandlers = addDotBtnsAndClickHandlers(emblaApi, dotsNode);
        emblaApi.on("destroy", removeDotBtnsAndClickHandlers);
    }
}
